*, *:before, *:after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
  transition: 0.2s;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

.app {
  min-height: 100vh;
  padding: 2rem;
  background-color: #ffb5b5;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.4rem;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

.title {
  margin-bottom: 2rem;
}

.main {
  display: flex;
  gap: 3rem;
}

.cards {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, minmax(100px, 1fr));
  grid-template-rows: repeat(auto, minmax(300px, 1fr));
  gap: 2rem;
}

.card {
  max-width: 19rem;
  height: 19rem;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
}
.card__img {
  pointer-events: none;
}

.hidden {
  background: #f04747;
}

.panel {
  padding: 3rem;
  border-radius: 5px;
  background-color: #eee;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.restart {
  padding: 1rem 2rem;
  border: none;
  background-color: #533bc0;
}